<template>
  <div class="personal-edit-page">
    <div class="header" :style="{ backgroundImage: `url(${avatar})` }">
      <div class="header-inner">
        <div class="avatar-wrap">
          <van-uploader :after-read="afterRead" capture="camera" :max-count="1">
            <div class="avatar-uploader">
              <van-image :src="avatar" round width="100%" height="100%" />
              <van-icon
                size="45"
                class="camera-icon"
                :name="require('@/assets/icon/take-photo-icon.png')"
              />
            </div>
          </van-uploader>
        </div>
        <van-field v-model="nickname" placeholder="请输入用户名" clearable />
      </div>
    </div>

    <div class="main">
      <van-cell-group>
        <van-cell title="性别">
          <div slot="right-icon" class="gender-wrap">
            <van-radio-group v-model="gender">
              <van-radio :name="0">男</van-radio>
              <van-radio :name="1">女</van-radio>
              <van-radio :name="2">保密</van-radio>
            </van-radio-group>
          </div>
        </van-cell>
        <van-field v-model="age" placeholder="请输入年龄" clearable />
        <van-field v-model="mobile" placeholder="请输入手机号" clearable />
        <van-field v-model="email" placeholder="请输入邮箱" clearable />
        <van-field v-model="address" placeholder="请输入地址" clearable />
        <van-field
          v-model="remark"
          rows="2"
          autosize
          type="textarea"
          maxlength="50"
          placeholder="请输入留言"
          show-word-limit
        />
      </van-cell-group>
    </div>
    <van-button @click="edit">next</van-button>
  </div>
</template>

<script>
import Vue from "vue";
import { Uploader, Field, Cell, CellGroup, Radio, RadioGroup } from "vant";
import * as Server from "@/server";
import { mapState, mapMutations } from "vuex";

Vue.use(Uploader)
  .use(Field)
  .use(CellGroup)
  .use(Cell)
  .use(Radio)
  .use(RadioGroup);
export default {
  data() {
    return {
      avatar: "",
      nickname: "",
      gender: 0,
      age: "",
      mobile: "",
      email: "",
      address: "",
      remark: "",
      filename: null
    };
  },
  computed: {
    ...mapState(["profile"])
  },
  methods: {
    ...mapMutations(["updateProfile"]),
    afterRead(file) {
      this.filename = file;
    },

    async edit() {
      const { status, data } = await Server.EditProfile({
        // avatar: this.avatar,
        nickname: this.nickname,
        gender: this.gender,
        age: this.age,
        mobile: this.mobile,
        email: this.email,
        address: this.address,
        remark: this.remark,
        filename: this.filename ? this.filename : undefined
      });
      if (status === 0) {
        console.log(data);
      }
    }
  },
  created() {
    console.log(this.profile);
    if (this.profile) {
      const {
        avatar,
        nickname,
        gender,
        age,
        mobile,
        email,
        address,
        remark
      } = this.profile;
      this.avatar = avatar;
      this.nickname = nickname;
      this.gender = gender;
      this.age = age;
      this.mobile = mobile;
      this.email = email;
      this.address = address;
      this.remark = remark;
    }
  }
};
</script>

<style lang="less">
.personal-edit-page {
  .header {
    background-repeat: no-repeat;
    background-size: 100% auto;
    background-position: center;
    .header-inner {
      background-color: rgba(29, 29, 29, 0.67);

      .van-cell {
        background-color: transparent;
      }
    }
    .avatar-wrap {
      display: flex;
      position: relative;
      height: 200px;
      align-items: center;
      justify-content: center;

      .avatar-uploader {
        width: 146px;
        height: 146px;
        position: relative;
        border-radius: 50%;
        background: #fff;
        // overflow: hidden;

        .camera-icon {
          position: absolute;
          right: 0;
          bottom: 0;
        }
      }
    }
  }
  .main {
    .gender-wrap {
      display: flex;

      & /deep/ .van-radio-group {
        display: flex;

        .van-radio + .van-radio {
          margin-left: 20px;
        }
      }
    }
  }
}
</style>
